@media (min-width : 1050px) {
    .name-container-box {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 45px;
        padding-top: 15%;
    }
    .greeting-box {
        font-size: 59px;
    }
    .myname-box {
        font-size: 110px;
    }
    .undergrad-box {
        font-size: 86px;
    }
}
@media (max-width : 1024px) {
    .container-box {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 45px;
        padding-top: 60%;
        line-height: 1.6;
    }
    .greeting-box {
        font-size: 35px;
        line-height: 1;
    }
    .myname-box {
        font-size: 43px;
    }
    .undergrad-box {
        font-size: 30px;
    }
}



