@media (min-width : 1050px) {
    .container-box {
        position: relative;
        width: 100%;
        font-size: 60px;
        top: 200px;
        line-height: 1.4;
    }
    .email-box {
        font-size: 80px;
    }
    .number-box {
        font-size: 103px;
    }
}
@media (max-width : 600px) {
    .container-box {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 35px;
    }
    .gap-div {
        padding-top: 15px;
    }
    .email-box {
        font-size: 36px;
    }
    .number-box {
        font-size: 47px;
    }
}


