@media (min-width : 1050px) {
    .interest-container-box {
        position: relative;
        width: 100%;
        font-size: 60px;
        top: 140px;
        line-height: 1.2;

    }
    .interest-box {
        font-size: 88px;
    }
    .fullstack-box {
        font-size: 95px;
    }
    .blockchain-box {
        font-size: 90px;
    }
    .iot-box {
        font-size: 67px;
    }
}
@media (max-width : 800px) {
    .interest-container-box {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 35px;
        padding-top: 55%;
        line-height: 1.4;
    }
    .gap-div {
        padding-top: 15px;
    }
    .interest-box {
        font-size: 33px;
    }
    .fullstack-box {
        font-size: 37px;
    }
    .blockchain-box {
        font-size: 35px;
    }
    .iot-box {
        font-size: 26px;
    }
}


