@media (min-width : 1050px) {
    .name-box {
        position: relative;
        width: 100%;
        font-size: 60px;
        top: 200px;
        line-height: 1.4;
    }
    .chennai-box {
        font-size: 120px;
    }
    .college-box {
        font-size: 33px;
    }
    .degree-box {
        font-size: 52px;
    }
}
@media (max-width : 600px) {
    .name-box {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 35px;
        top: 100px;
        line-height: 2;
        padding-top: 23%;
    }
    .gap-div {
        padding-top: 15px;
    }
    .chennai-box {
        font-size: 45px;
        line-height: 1;
    }
    .college-box {
        font-size: 30px;
        line-height: 1;
    }
    .degree-box {
        font-size: 30px;
        line-height: 1;
    }
}


