@media (min-width : 1050px) {
    .skills-container-box {
        position: relative;
        width: 100%;
        font-size: 60px;
        top: 190px;
        line-height: 1.4;
    }
    .js-box {
        font-size: 47px;
    }
    .python-box {
        font-size: 50px;
    }
    .sql-box {
        font-size: 77px;
    }
    .also-box {
        font-size: 59px;
    }
}
@media (max-width : 600px) {
    .skills-container-box {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 35px;
        padding-top: 42%;
    }
    .gap-div {
        padding-top: 20px;
    }
    .js-box {
        font-size: 30px;
    }
    .python-box {
        font-size: 30px;
    }
    .sql-box {
        font-size: 30px;
    }
    .also-box {
        font-size: 30px;
    }
}


